@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  color: #696969;
}

.slider {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 200px;
  height: 200px;
  margin: auto;
  perspective: 600px;
}

.slider * {
  transition: all 1s cubic-bezier(0.5, -0.75, 0.2, 1.5);
}

.container {
  width: inherit;
  height: inherit;
  transform-style: preserve-3d;
  transform: rotateY(0deg) rotateX(0deg);
}

.slide,
.slide:after,
.slide:before {
  display: block;
  width: inherit;
  height: inherit;
  position: absolute;
  transform-style: preserve-3d;
  background-size: cover;
  background-position: center;
}

.slide.x {
  transform: rotateY(90deg);
}

.slide.x:after {
  content: "";
  background-image: url(/static/media/JavaScript.7df79562.jpg);
  transform: translateZ(100px) rotateZ(-90deg);
}

.slide.x:before {
  content: "";
  background-image: url(/static/media/Css.dd201e2b.jpeg);
  transform: translateZ(-100px) rotateZ(-90deg);
}

.slide.y {
  transform: rotateX(90deg);
}

.slide.y:after {
  content: "";
  background-image: url(/static/media/Html.04ff2a69.svg);
  transform: translateZ(100px) scale(-1);
}

.slide.y:before {
  content: "";
  background-image: url(/static/media/React.f87ffcca.jpg);
  transform: translateZ(-100px);
}

.slide.z {
  transform: rotateX(0);
}

.slide.z:after {
  content: "";
  background-image: url(/static/media/Ubuntu.920db6df.jpg);
  transform: translateZ(100px);
}

.slide.z:before {
  content: "";
  background-image: url(/static/media/It.3ab2d6bb.png);
  transform: translateZ(-100px);
}

.container {
  animation: rotate 20s infinite cubic-bezier(1, -0.75, 0.5, 1.2);
}

@keyframes rotate {
  0%,
  10% {
    transform: rotateY(0deg) rotateX(0deg);
  }
  15%,
  20% {
    transform: rotateY(180deg) rotateX(0deg);
  }
  25%,
  35% {
    transform: rotateY(180deg) rotateX(270deg);
  }
  40%,
  50% {
    transform: rotateY(180deg) rotateX(90deg);
  }
  55%,
  65% {
    transform: rotateY(-90deg) rotateX(90deg);
  }
  70%,
  80% {
    transform: rotateY(90deg) rotateX(90deg);
  }
  90%,
  95% {
    transform: rotateY(0deg) rotateX(90deg);
  }
}

.shadow {
  display: block;
  width: 200px;
  height: 200px;
  background: rgba(0, 0, 0, 0.75);
  position: absolute;
  top: 60%;
  transform: rotateX(90deg);
  z-index: -1;
  filter: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg"><filter id="filter"><feGaussianBlur stdDeviation="20" /></filter></svg>#filter');
  filter: blur(20px);
  left: 0;
  right: 0;
  margin: auto;
  animation: rotateShadow 15s infinite cubic-bezier(1, -0.75, 0.5, 1.2);
}

