/* 
Credit */

.credit {
  position: fixed;
  bottom: 22px;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  text-align: center;
}

.credit a {
  font-weight: 900;
  color: blue;
  text-decoration: none;
  transition: all 0.15s linear;
  background: url(https://dl.dropboxusercontent.com/u/12646264/assets/open-in-new-window-icon.png)
    no-repeat right;
  background-size: 12px;
  padding-right: 20px !important;
  -filter: invert(1);
  -webkit-filter: invert(1);
  -moz--filter: invert(1);
  -o--filter: invert(1);
}

.credit a:hover {
  color: tomato;
}

.small {
  font-size: smaller;
  opacity: 0.75;
}

